<script>

import axios from "axios";
import {baseUrl} from "../config";

export default {
  components: {

  },
  data() {
    return{
      page: 1,
      currentPage: 1,
      tests_id:'9',
      regions:[],
      regions_id:'',
      districts:[],
      districts_id:'',
      organizations:[],
      organizations_id:'',
      statsExel:[],
      tests:[],
      test_result:[],
      all_user:0,
      quiz_user:0,
      class_user:0,
      men_user:0,
      women_user:0,
      nine_class:0,
      eleven_class:0
    }
  },
  methods: {
    getDistrct() {
      axios.get(`https://api-kasb.maktab.uz/api/v1/integration/districts/?region_id=${this.regions_id}`)
        .then(res => {
          console.log(res.data)
          this.districts = res.data
        })
      axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?reg_id=${this.regions_id}`, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language':'uz'
          }
        }
      )
        .then(res=>{
          console.log(res.data)
          this.test_result=res.data
        })
    },
    getOr() {

      axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?school_id=${this.organizations_id}`, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language':'uz'
          }
        }
      )
        .then(res=>{
          console.log(res.data)
          this.test_result=res.data
        })
    },
    getOrDis() {
      axios.get(`https://api-kasb.maktab.uz/api/v1/integration/organizations/?region_id=${this.regions_id}&district_id=${this.districts_id}`)
        .then(res => {
          console.log(res.data)
          this.organizations = res.data
        })
      axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?district_id=${this.districts_id}`, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language':'uz'
          }
        }
      )
        .then(res=>{
          console.log(res.data)
          this.test_result=res.data
        })
    },
    getResult() {
      axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?class_id=${this.tests_id}`, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language':'uz'
          }
        }
      )
        .then(res=>{
          console.log(res.data)
          this.test_result=res.data
        })
    }
  },
  mounted() {
    axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?class_id=9`, {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
          'Accept-Language':'uz'
        }
      }
    )
      .then(ress=>{
        axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?class_id=11`, {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
              'Accept-Language':'uz'
            }
          }
        )
          .then(res=>{
            console.log(res.data)
            this.nine_class=ress.data.total
            this.eleven_class=res.data.total
            this.quiz_user=res.data.total+ress.data.total
          })
      })
    axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?gender_id=1`, {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
          'Accept-Language':'uz'
        }
      }
    )
      .then(ress=>{
        axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?gender_id=2`, {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
              'Accept-Language':'uz'
            }
          }
        )
          .then(res=>{
            console.log(res.data)
            this.men_user=res.data.total
            this.women_user=ress.data.total
          })
      })

    axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/`, {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
          'Accept-Language':'uz'
        }
      }
    )
      .then(res=>{
        console.log(res.data)
        this.all_user=res.data.total
      })
    axios.get(`https://api-kasb.maktab.uz/api/v1/integration/organizations/?region_id=${this.$route.params.reg_id}&district_id=${this.$route.params.des_id}`)
      .then(res => {
        console.log(res.data)
        this.organizations = res.data
      })
    axios.get(`https://api-kasb.maktab.uz/api/v1/integration/districts/?region_id=${this.$route.params.reg_id}`)
      .then(res => {
        console.log(res.data)
        this.districts = res.data
      })
    axios.get(`${baseUrl}tests/`)
      .then(res=>{
        this.tests=res.data
        console.log(res)
      })

    axios.get('https://api-kasb.maktab.uz/api/v1/integration/regions/')
      .then(res=>{
        this.regions=res.data
      })
    console.log(this.$route.params)
    axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?class_id=9`, {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
          'Accept-Language':'uz'
        }
      }
    )
      .then(res=>{
        console.log(res.data)
        this.test_result=res.data
      })
  },
  watch: {
    page(page) {
      axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?page=${page}&&class_id=${this.tests_id}`, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language':'uz'
          }
        }
      )
        .then(res=>{
          console.log(res.data)
          this.test_result=res.data
        })
    }
  },
};
</script>
